import React, { useEffect, useState } from "react";
import "./SearchBar.css";
import SearchIcon from "@mui/icons-material/Search";
import CloseIcon from "@mui/icons-material/Close";
// import fetch from 'node-fetch';
import SERVER_URL from "../Constants";
// import useFetch from './Fetch';
// import Loading from './Loading';
import axios from "axios";
import { CircularProgress } from "@mui/material";
import { toast } from "react-toastify";
import { socket } from "../Socket";
const SearchBar = ({ placeholder, marginTop, mode }) => {
	const [filteredData, setFilteredData] = useState([]);
	const [wordEntered, setWordEntered] = useState("");
	const [loading, setLoading] = useState(false);
	// const controller = new AbortController();
	// const { data, loading, error, refetch } = useFetch(SERVER_URL + '/api/search?q=' + wordEntered);

	useEffect(() => {
		const DelayRequest = setTimeout(async () => {
			if (wordEntered === "") {
				console.log("triggered");
				setFilteredData([]);
			} else {
				setLoading(true);
				const response = await axios.get(SERVER_URL + "/api/search?q=" + wordEntered);
				// const response = await (await fetch(SERVER_URL + '/api/search?q=' + wordEntered)).json();
				// console.log(wordEntered);
				if (wordEntered) {
					// console.log('wordEntered if is triggered');
					setFilteredData(response.data.data);
				} else {
					console.log("triggered cleanup");
					setFilteredData([]);
				}
				setLoading(false);
			}
		}, 500);
		return () => clearTimeout(DelayRequest);
	}, [wordEntered]);
	const handleFilter = async (event) => {
		const searchWord = event.target.value;
		setWordEntered(searchWord);
		setLoading(true);
		if (searchWord === "") {
			setFilteredData([]);
			setTimeout(() => {
				console.log("Clear request triggered");
			}, 300);
		}
	};
	const clearInput = () => {
		console.log("Cleared is triggered");
		setFilteredData([]);
		setWordEntered("");
	};
	/**
	 * @param {(event: React.MouseEventHandler<HTMLButtonElement>)} event
	 */
	const Button = (event) => {
		// console.log(key);
		// console.log(event.target.id);
		if (!event.target.id) return toast.error("Link Kosong");
		if (!socket.connected) toast.error("Tidak dapat terhubung dengan server ...");
		else {
			if (mode === "user") {
				const data = {
					link: event.target.id,
					mode: "user"
				};
				socket.emit("get_metadata", data);
			} else if (mode === "admin") {
				const data = {
					link: event.target.id,
					mode: "admin"
				};
				socket.emit("get_metadata", data);
			}
		}
	};
	// console.log(filteredData.length);
	return (
		<div className="search">
			<div className="searchInputs" style={{ marginTop }}>
				<input type="text" placeholder={placeholder} value={wordEntered} onChange={handleFilter} />
				<div className="searchIcon">
					{filteredData.length === 0 ? loading && wordEntered ? <CircularProgress style={{ marginRight: "10px" }} /> : <SearchIcon /> : <CloseIcon id="clearBtn" onClick={clearInput} />}
				</div>
			</div>
			{
				<div className="dataResult">
					{filteredData.length !== 0 && (
						<div className="dataResult">
							{filteredData.map((value) => {
								return (
									<button className="dataItem" onClick={Button} id={value.link} link={value.link} key={value.link}>
										<img src={value.image} alt="" height="60px" />
										<p key={value.title} id={value.link}>
											{value.title} ({value.year}) {value.quality}
										</p>
									</button>
								);
							})}
						</div>
					)}
				</div>
			}
		</div>
	);
};

export default SearchBar;
