import React from "react";
import { toast } from "react-toastify";
function Test() {
	const toastId = React.useRef(null);

	const notify = () => (toastId.current = toast("Hello", { autoClose: true }));

	const update = () => {
		// React.useRef()
		console.log(toastId.current);
		toast.update(toastId.current, { type: toast.TYPE.INFO, autoClose: 5000, hideProgressBar: true });
	};

	return (
		<div>
			<button onClick={notify}>Notify</button>
			<button onClick={update}>Update</button>
		</div>
	);
}

export default Test;
