import React, { useState } from 'react';
import { socket } from '../Socket';
import './ClientStatus.css';
import RedDot from '../Assets/RedDot.png';
import GreenDot from '../Assets/GreenDot.png';
function ClientStatus() {
    const [homeStatus, setHomeStatus] = useState(RedDot);
    const [downloaderStatus, setDownloaderStatus] = useState(RedDot);
    socket.emit('get_client_status');
    socket.on('client_status', (data) => {
        // console.log(data);
        const isHomeActive = data.home_client_is_connected;
        const isDownloaderActive = data.downloader_client_is_connected;
        if (isHomeActive) setHomeStatus(GreenDot);
        else setHomeStatus(RedDot);
        if (isDownloaderActive) setDownloaderStatus(GreenDot);
        else setDownloaderStatus(RedDot);
    });
    socket.on('disconnect', () => {
        setHomeStatus(RedDot);
        setDownloaderStatus(RedDot);
    });
    return (
        <div className="ClientStatus">
            <table cellPadding="10">
                <tbody>
                    <tr>
                        <th scope="col">Home Status</th>
                        <th scope="col">Downloader Status</th>
                    </tr>
                    <tr>
                        <td>
                            <img src={homeStatus} alt="" width="20px" />
                        </td>
                        <td>
                            <img src={downloaderStatus} alt="" width="20px" />
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

export default ClientStatus;
