import React, { useEffect } from "react";
import { socket } from "../Socket";
import { toast } from "react-toastify";
import SearchBar from "./SearchBar";
import "./Clients.css";
function Clients() {
	useEffect(() => {
		socket.on("connect", () => {
			// setIsConnected(true)
			// setStatus(true);
			toast.success("WebSocket is Connected");
		});
		socket.on("ping", (data) => {
			toast(data);
		});
		socket.on("disconnect", () => {
			toast.error("WebSocket failed to Connect");
		});
		socket.on("notifications", (data) => {
			toast(data.message, {
				type: data.level
			});
		});
	}, []);
	return (
		<div className="Clients">
			<SearchBar placeholder="Mau cari film apa ..." marginTop="120px" mode="user" />
		</div>
	);
}

export default Clients;
