import { useRef, useEffect, useState } from "react";
// import { io } from 'socket.io-client';
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./Admin.css";
import SearchBar from "./SearchBar";
import { socket } from "../Socket";
import ClientStatus from "./ClientStatus";
import Constants from "../Constants";
// import { bytesToSize, formatAsPercent } from "../Utils/Additional";
// import SERVER_URL from './Constants';
// const socket = io(SERVER_URL, {
//     path: '/ws'
// });

function App() {
	const [data, setData] = useState(undefined);
	const [status, setStatus] = useState(false);
	const toastId = useRef(null);
	const token = localStorage.getItem("token");
	useEffect(() => {
		axios({
			method: "POST",
			url: `${Constants}/api/verify?token=${token}`
		}).then(({ data }) => {
			if (!data.isVerified) window.location.href = "/login";
		});
		socket.on("connect", () => {
			// setIsConnected(true)
			// setStatus(true);
			toast.success("WebSocket is Connected");
		});
		socket.on("ping", (data) => {
			toast(data);
		});
		socket.on("disconnect", () => {
			toast.error("WebSocket failed to Connect");
		});
		socket.on("notifications", (data) => {
			toast(data.message, {
				type: data.level
			});
		});
		socket.on("progress", (data) => {
			console.log("Received progress event");
			if (!toast.isActive(toastId.current)) {
				toastId.current = toast(data.message, {
					toastId: data.id
				});
			} else {
				toast.update(toastId.current, {
					toastId: data.id,
					render: data.message,
					hideProgressBar: true
				});
			}
		});
		if (toastId.current === "download") {
			toast.success("Mengunduh film");
		}
	}, []);
	// console.log(socket.listeners("connect"));
	if (!token) return (window.location.href = "/login");
	return (
		<>
			<div className="App">
				<ClientStatus />
				<SearchBar placeholder="Mau cari film apa ..." marginTop="30px" mode="admin" />
			</div>
			{/* <h1>jembud</h1> */}
		</>
	);
}

export default App;
