import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Login.css";
import { toast } from "react-toastify";
// import useFetch from "./Fetch";
import axios from "axios";
import Constants from "../Constants";
function Login() {
	const [username, setUsername] = React.useState("");
	const [password, setPassword] = React.useState("");
	console.log(username);
	const HandleButton = async (e) => {
		e.preventDefault();
		if (!username & !password) toast.warning("Masukkan Username dan Password!");
		else if (!username) toast.warning("Masukkan Username!");
		else if (!password) toast.warning("Masukkan Password");
		else {
			const { data } = await axios({
				method: "POST",
				url: `${Constants}/api/login`,
				headers: {
					Authorization: "Basic " + btoa(`${username}:${password}`)
				}
			});
			if (data.token === null) {
				toast.error("Username dan Password salah!");
				return;
			} else {
				localStorage.setItem("token", data.token);
				sessionStorage.setItem("status", "logged");
				window.location.href = "/admin";
			}
		}
	};
	return (
		<div className="Auth-form-container">
			<form className="Auth-form">
				<div className="Auth-form-content">
					<h3 className="Auth-form-title">Sign In</h3>
					<div className="form-group mt-3">
						<label>Username</label>
						<input type="email" className="form-control mt-1" placeholder="Enter username" onChange={(e) => setUsername(e.target.value)} />
					</div>
					<div className="form-group mt-3">
						<label>Password</label>
						<input type="password" className="form-control mt-1" placeholder="Enter password" onChange={(e) => setPassword(e.target.value)} />
					</div>
					<div className="d-grid gap-2 mt-3 pb-4">
						<button className="btn btn-primary" onClick={HandleButton}>
							Login
						</button>
					</div>
				</div>
			</form>
		</div>
	);
}

export default Login;
